import { Nav, NavbarContainer, NavLogo, NavMenu } from "./indexElements";
import { Button } from "@mui/material";
import { colours } from "../../../styles/colours";
// import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import React from "react";

interface params {
  windowSize: {
    width: number;
    height: number;
  };
}

const Navbar: React.FC<params> = (props) => {
  const buttonStyle = {
    marginRight: 0,
    marginLeft: 0,
    borderRadius: "0.4vh",
    paddingTop: "0.3vh",
    paddingBottom: "0.3vh",
    paddingLeft: "0.7vh",
    paddingRight: "0.7vh",
    fontSize: "1.17vh",
    width: "fit-content",
    border: `0.01vh solid LightGrey`,
    color: colours.CFIA_Font_Black,
    "&:hover": {
      backgroundColor: "#F5F5F5",
      transition: "0.1s ease-in-out all",
      border: `0.01vh solid LightGrey`,
    },
  };
  return (
    <Nav width={props.windowSize.width} height={props.windowSize.height}>
      <NavbarContainer
        width={props.windowSize.width}
        height={props.windowSize.height}
      >
        <NavLogo
          src={require("../../../assets/CFIA_blackfont.png")}
          alt="CFIA Logo"
          width={props.windowSize.width}
        />
        <NavMenu>
          <Button
            variant="outlined"
            onClick={() => {
              console.log("sign in");
            }}
            sx={buttonStyle}
          >
            SIGN IN
          </Button>
          {/* <IconButton
            sx={{ padding: 0, marginTop: "0.27vh", marginRight: "0.4vh" }}
            onClick={() => {
              console.log("account");
            }}
          >
            <AccountCircleIcon
              style={{
                color: colours.CFIA_Font_Black,
                fontSize: "3vh",
                marginTop: 0,
                marginBottom: 0,
                paddingTop: 0,
                paddingBottom: 0,
                marginRight: 0,
                marginLeft: 0,
              }}
            />
          </IconButton> */}
        </NavMenu>
      </NavbarContainer>
    </Nav>
  );
};

export default Navbar;
